import { CategoriesChildrenListDataModel } from 'api/categories/models/CategoriesChildrenModel';
import Col from 'components/UI/Col';
import Content from 'components/UI/Content';
import Title from 'components/UI/Title';

import React from 'react';
import { useSelector } from 'react-redux';

import styles from './Categories.module.scss';
import CategoriesList from './views/CategoriesList';

type CategoriesTypes = {
	categories: CategoriesChildrenListDataModel;
};

const Categories = ({ categories }: CategoriesTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	return (
		<Content>
			<>
				<Col className={styles.categoriesCol} marginBottom={isMobile ? 40 : 60}>
					<Title size={isMobile ? 16 : 20} withDot text={categories[0].parent || 'Все'} />
				</Col>
				<CategoriesList categoriesList={categories} />
			</>
		</Content>
	);
};

export default React.memo(Categories);
