import ActiveLink from 'components/ActiveLink';
import Col from 'components/UI/Col';
import Title from 'components/UI/Title';

import routes from 'helpers/routes';
import Image from 'next/image';
import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';

import styles from './CategoriesList.module.scss';
import { useRouter } from 'next/router';

type CategoriesListTypes = {
	categoriesList: {
		name: string;
		count: number;
		img: string;
		redirectTo: string;
		stop: boolean;
	}[];
};

const CategoriesList = ({ categoriesList }: CategoriesListTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const router = useRouter();

	const allCount = useMemo(() => {
		return categoriesList.reduce((acc, item) => (acc += item.count), 0);
	}, [categoriesList]);

	return (
		<Col>
			<div className={styles.categories}>
				{categoriesList.length > 1 && (
					<ActiveLink href={routes.catalog + router.query.name}>
						<>
							<Title
								className={styles.title}
								withSuisse
								size={isMobile ? 32 : 80}
								text={'Посмотреть все'}
								count={allCount}
							/>
							<div></div>
						</>
					</ActiveLink>
				)}
				{categoriesList.map(item => {
					return (
						<ActiveLink key={item.name} href={(!item.stop ? routes.categories : routes.catalog) + item.redirectTo}>
							<>
								<Title
									className={styles.title}
									withSuisse
									size={isMobile ? 32 : 80}
									text={item.name}
									count={item.count}
								/>
								{!isMobile ? (
									<Image
										alt={item.name}
										src={item.img}
										width={158}
										height={232}
										style={{
											maxWidth: '100%',
											height: 'auto',
										}}
									/>
								) : (
									<div></div>
								)}
							</>
						</ActiveLink>
					);
				})}
			</div>
		</Col>
	);
};

export default React.memo(CategoriesList);
