import CategoriesChildrenListApi from 'api/categories/CategoriesChildren';
import Footer from 'components/Footer';
import Header from 'components/Header';
import Categories from 'components/pages/Categories';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { useGetUserData } from 'helpers/hooks/useGetUserData';
import { baseSSR } from 'helpers/utils/baseSSR';
import { serverSideTranslations } from 'next-i18next/serverSideTranslations';
import React from 'react';
import { changeIsMobile } from 'store/isMobile/IsMobileStore';

const CatigoriesPage = ({ data }) => {
	const dispatch = useAppDispatch();

	dispatch(changeIsMobile(data.isMobile));

	useGetUserData();

	return (
		<>
			<Header />
			<Categories categories={data.categories} />
			<Footer withDarkBGC={true} />
		</>
	);
};

export const getServerSideProps = async ({ req, params, locale }) => {
	const { isMobile } = await baseSSR(req);
	const categoriesChildrenListApi = new CategoriesChildrenListApi();
	const categories = await categoriesChildrenListApi.getCategoriesChildrenListData({
		parent: params.name === 'all' ? '' : params.name,
	});

	if (!Array.isArray(categories.data) || (Array.isArray(categories.data) && categories.data.length === 0)) {
		return {
			notFound: true,
		};
	}

	return {
		props: {
			data: {
				categories: categories.data,
				isMobile,
			},
			...(await serverSideTranslations(locale, ['common'])),
		},
	};
};

export default CatigoriesPage;
